@mixin heading01 {
  font-size: var(--app-font-size-heading-01);
  font-weight: var(--app-font-weight-heading-01);
  font-variation-settings: 'wght' var(--app-font-weight-heading-01);
  font-family: var(--app-font-family-heading-01);
  line-height: var(--app-line-height-heading);
}

@mixin heading02 {
  font-size: var(--app-font-size-heading-02);
  font-weight: var(--app-font-weight-heading-02);
  font-variation-settings: 'wght' var(--app-font-weight-heading-02);
  font-family: var(--app-font-family-heading-02);
  line-height: var(--app-line-height-heading);
}

@mixin heading03($fontWeight: var(--app-font-weight-heading-03)) {
  font-size: var(--app-font-size-heading-03);
  font-weight: $fontWeight;
  font-variation-settings: 'wght' $fontWeight;
  font-family: var(--app-font-family-heading-03);
  line-height: var(--app-line-height-heading);
}

@mixin heading03a {
  font-size: var(--app-font-size-heading-03a);
  font-weight: var(--app-font-weight-heading-03);
  font-variation-settings: 'wght' var(--app-font-weight-heading-03);
  font-family: var(--app-font-family-heading-03);
  line-height: var(--app-line-height-heading);
}
@mixin heading03b {
  font-size: var(--app-font-size-heading-03b);
  font-weight: var(--app-font-weight-heading-03);
  font-variation-settings: 'wght' var(--app-font-weight-heading-03);
  font-family: var(--app-font-family-heading-03);
  line-height: var(--app-line-height-heading);
}
@mixin heading04 {
  font-size: var(--app-font-size-heading-04);
  font-weight: var(--app-font-weight-heading-04);
  font-variation-settings: 'wght' var(--app-font-weight-heading-04);
  font-family: var(--app-font-family-heading-04);
  line-height: var(--app-line-height-heading);
}

@mixin heading04a {
  font-size: var(--app-font-size-heading-04a);
  font-weight: var(--app-font-weight-heading-04);
  font-variation-settings: 'wght' var(--app-font-weight-heading-04);
  line-height: var(--app-line-height-heading);
}

@mixin heading05($lineHeight: var(--app-line-height-text-long)) {
  font-size: var(--app-font-size-heading-05);
  font-weight: var(--app-font-weight-heading-05);
  font-variation-settings: 'wght' var(--app-font-weight-heading-05);
  line-height: $lineHeight;
  font-family: var(--app-font-family-heading-05);
}

@mixin copy01 {
  font-size: var(--app-font-size-copy-01);
  font-weight: var(--app-font-weight-copy-01);
  font-variation-settings: 'wght' var(--app-font-weight-copy-01);
}

@mixin copy02 {
  font-size: var(--app-font-size-copy-02);
