  font-variation-settings: 'wght' var(--app-font-weight-copy-02);
}

@mixin copy03($fontWeight: var(--app-font-weight-copy-03), $lineHeight: null) {
  font-size: var(--app-font-size-copy-03);
  font-weight: $fontWeight;
  font-variation-settings: 'wght' $fontWeight;
  line-height: $lineHeight;
}

@mixin copy04 {
  font-size: var(--app-font-size-copy-04);
}

@mixin copy05($fontWeight: var(--app-font-weight-copy-03), $lineHeight: null) {
  font-size: var(--app-font-size-copy-05);
  font-weight: $fontWeight;
  line-height: $lineHeight;
}
