  font-size: var(--app-font-size-copy-06);
  font-weight: $fontWeight;
  font-variation-settings: 'wght' $fontWeight;
  line-height: $lineHeight;
}
@mixin link01 {
  font-size: var(--app-font-size-link-01);
  font-variation-settings: 'wght' 200;
}

@mixin menuItemMobile {
  font-size: var(--app-font-size-menu-small);
  font-variation-settings: 'wght' 200;
}

@mixin menuItemMobileLevel {
  font-size: var(--app-font-size-menu-small-level);
  font-variation-settings: 'wght' 200;
}
