//*
//* #LoginForm
//**********************************************************/
@use "../../../../styles/settings/typos" as typos;

.host {
  display: flex;
  width: 100%;
  flex: 1;
  flex-direction: column;
  position: relative;
  margin-top:40px;
  margin-bottom: 100px
}


.title {
  @include typos.heading01;
}

.text {
  @include typos.copy02;
  margin: var(--app-spacing-01) 0 var(--app-spacing-04);
}

.row {
  margin-bottom: var(--app-spacing-02);
  width: 100%;
  &[app-variant='second-row']{
    margin-top:8px;
  }
  &[app-variant='third-row']{
    margin-top:16px;
    
  }
}

.buttons {
  margin-top: var(--app-spacing-04);
}

.checkbox {
  display: flex;
  flex-direction: row;
  flex: 0;
  padding-left: 2.5px;
  justify-content: flex-start;
  align-items: center;
  margin: 0.75ex 0 0.75ex 0;
}
.checkbox [data-reach-custom-checkbox-container][data-state="checked"]:after {
  display: block;
  width: calc(45% - 2px);
  height: calc(90% - 4px);
  border: solid #fff;
  border-width: 0 0.25em 0.25em 0;
  -webkit-transform: translateY(-65%) translateX(-50%) rotate(45deg);
  transform: translateY(-65%) translateX(-50%) rotate(45deg);
}
.checkboxLabel {
  flex-shrink: 1;
  flex-grow: 1;
  cursor: pointer;
  word-wrap: break-word;
  max-width: 100%;
  min-width: 200px;
  color: #9b9b9b;
  font-family: var(--app-font-quicksand-medium);
  font-size: 1.1em;
  font-weight: 500;
  letter-spacing: 0;
  line-height: 24px;
  margin: 0;
  padding: 0;
  display: flex;
  flex: 1;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 0.75ex;
}
.signInButton {
  margin: 0;
  width: 100%;
 // position: relative;
 display: flex;
 justify-content: center;
}
 

.errorMessage{
  font-family: var(--app-font-quicksand-medium);
  font-size: 12px;
  color: var(--app-color-red-error);
  white-space: pre;
  word-break: break-all;
  align-self: center;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 10px 0;
}

.recoverPasswordButton {
  align-self: flex-start;
  text-decoration: underline;
  text-underline-offset: 4px;
  text-align: start;
}

.recoverPassword {
  margin-top: 10px;
  color: blue;
}