.inputContainer{
  flex:1;
  width: 100%;
  height:56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--app-color-gray-lighter);
  border-radius: 2px;
  padding: 18px 24px;
  background-color: pink;
}



.ornament {
  align-self: center;
}
.rightButton {
  cursor: pointer;
  outline: none;
  border: none;
  background-color: transparent;

  font-family: var(--app-font-quicksand-bold);
  font-weight: 700;
  font-size: 16px;
  color: var(--app-color-gray-light);
}

.inputContainer {
  display: flex;
  justify-content: space-between;
  position: relative;
  background-color: var(--app-color-white-lightest);
}
.textInput {
  flex:1;
  border:none;
  outline:none;
  width: 100%;
  font-family: var(--app-font-quicksand-medium);
  font-size: 16px;
  font-weight: 700;
  text-align: left;
  color: var(--app-color-gray-dark) !important;
 

  &[app-readonly="readonly"] {
    pointer-events: none;
    font-family: var(--app-font-quicksand-medium);
    font-size: 16px;
    text-align: left;
    color: var(--app-color-gray-dark) !important;
  }
  &::placeholder{
    font-family: var(--app-font-quicksand-medium);
    font-size: 16px;
    text-align: left;
    color: var(--app-color-gray-light);
  }
}
.inputLabel {
  flex:1;
  color:var(--app-color-gray-dark);
  font-family: var(--app-font-quicksand-medium);
  font-style: normal;
  font-weight: 500;
  font-size: 1em;
  text-align: left;
}
.errorContainer{
  font-family: var(--app-font-quicksand-medium);
  font-size: 12px;
  color: var(--app-color-red-error);
  white-space: pre;
  word-break: break-all;
  align-self: left;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 10px 0;
}